"use client";

import React, { useState } from "react";
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Button } from "@/components/ui/button";

const tokens = [
  {
    name: "Donald Trump",
    symbol: "DNT",
    description: "MAGA DEGEN GOGOGOGO",
    website: null,
    telegram: null,
    twitter: null,
    address: "CoJUvBkduCa8bUCk4Mjv6dcizTYpw7rMJGJJ77zUFuKQ",
    ipfs: "https://ipfs.io/ipfs/QmadiqcFdy4K6D4F6bNmazAAwauiNowjebGYdeUFA6LPdM",
  },
  // Add more tokens if needed
];

const fundingWallets = [
  {
    balanceSOL: 0.006002,
    balanceUSDT: 1.24,
    address: "G92h3wp62hHyqoGyXR8fgiXULkFvwWFHvHKtokAp6Csq",
  },
  // Add more wallets if needed
];

export default function Page() {
  const [selectedToken, setSelectedToken] = useState(tokens[0]);
  const [selectedFundingWallet, setSelectedFundingWallet] = useState(fundingWallets[0]);
  const [devWallet, setDevWallet] = useState("Random address");
  const [buyerWallets, setBuyerWallets] = useState(["0.255885701", "0.285516516", "0.458597783"]);
  const [totalBuyAmount, setTotalBuyAmount] = useState("1");

  const addBuyerWallet = () => {
    setBuyerWallets([...buyerWallets, ""]);
  };

  const deleteBuyerWallet = () => {
    if (buyerWallets.length > 0) {
      setBuyerWallets(buyerWallets.slice(0, -1));
    }
  };

  const equalBuy = () => {
    const equalAmount = (parseFloat(totalBuyAmount) / buyerWallets.length).toFixed(9);
    setBuyerWallets(buyerWallets.map(() => equalAmount));
  };

  const randomBuy = () => {
    let remaining = parseFloat(totalBuyAmount);
    const amounts = [];
    for (let i = 0; i < buyerWallets.length - 1; i++) {
      const val = parseFloat((Math.random() * remaining).toFixed(9));
      amounts.push(val);
      remaining -= val;
    }
    amounts.push(parseFloat(remaining.toFixed(9)));
    setBuyerWallets(amounts.map((a) => a.toString()));
  };

  const updateBuyerWalletAmount = (index: number, value: string) => {
    const newWallets = [...buyerWallets];
    newWallets[index] = value;
    setBuyerWallets(newWallets);
  };

  return (
    <div className="space-y-6">
      {/* 1. Select Token to Launch */}
      <Card className="border border-blue-500 p-4">
        <h2 className="mb-2 font-semibold">1. Select Token to Launch</h2>
        <Select
          value={selectedToken.name}
          onValueChange={(val) => {
            const token = tokens.find((t) => t.name === val);
            if (token) setSelectedToken(token);
          }}
        >
          <SelectTrigger>
            <SelectValue />
          </SelectTrigger>
          <SelectContent>
            {tokens.map((token) => (
              <SelectItem key={token.name} value={token.name}>
                {token.name} ({token.symbol}) - {token.address}
              </SelectItem>
            ))}
          </SelectContent>
        </Select>
        <div className="mt-4 space-y-1 text-sm">
          <p><strong>Name:</strong> {selectedToken.name}</p>
          <p><strong>Symbol:</strong> {selectedToken.symbol}</p>
          <p><strong>Description:</strong> {selectedToken.description}</p>
          <p><strong>Website:</strong> {selectedToken.website ?? "null"}</p>
          <p><strong>Telegram:</strong> {selectedToken.telegram ?? "null"}</p>
          <p><strong>Twitter:</strong> {selectedToken.twitter ?? "null"}</p>
          <p><strong>Address:</strong> {selectedToken.address}</p>
          <p><strong>IPFS:</strong> <a href={selectedToken.ipfs} target="_blank" rel="noreferrer" className="text-blue-400 underline">{selectedToken.ipfs}</a></p>
        </div>
      </Card>

      {/* 2. Select Funding Wallet */}
      <Card className="border border-blue-500 p-4">
        <h2 className="mb-2 font-semibold">2. Select Funding Wallet</h2>
        <Select
          value={selectedFundingWallet.address}
          onValueChange={(val) => {
            const wallet = fundingWallets.find((w) => w.address === val);
            if (wallet) setSelectedFundingWallet(wallet);
          }}
        >
          <SelectTrigger>
            <SelectValue />
          </SelectTrigger>
          <SelectContent>
            {fundingWallets.map((wallet) => (
              <SelectItem key={wallet.address} value={wallet.address}>
                Balance: {wallet.balanceSOL.toFixed(9)} SOL ({wallet.balanceUSDT} USDT) - {wallet.address}
              </SelectItem>
            ))}
          </SelectContent>
        </Select>
      </Card>

      {/* 3. Select Dev Wallet */}
      <Card className="border border-blue-500 p-4">
        <h2 className="mb-2 font-semibold">3. Select Dev Wallet</h2>
        <Input
          value={devWallet}
          onChange={(e) => setDevWallet(e.target.value)}
          placeholder="Random address"
        />
      </Card>

      {/* 4. Add Buyer Wallets */}
      <Card className="border border-blue-500 p-4">
        <h2 className="mb-2 font-semibold">4. Add Buyer Wallets</h2>
        <div className="mb-2 flex items-center gap-2">
          <label className="mr-2">Total buy amount (SOL)</label>
          <Input
            type="number"
            value={totalBuyAmount}
            onChange={(e) => setTotalBuyAmount(e.target.value)}
            className="w-24"
          />
        </div>
        <div className="mb-2 flex gap-2">
          <Button onClick={addBuyerWallet} variant="default" size="sm">Add wallet</Button>
          <Button onClick={deleteBuyerWallet} variant="default" size="sm">Delete wallet</Button>
          <Button onClick={equalBuy} variant="outline" size="sm">Equal buy</Button>
          <Button onClick={randomBuy} variant="outline" size="sm">Random buy</Button>
        </div>
        <div className="space-y-1">
          {buyerWallets.map((amount, index) => (
            <Input
              key={index}
              value={amount}
              onChange={(e) => updateBuyerWalletAmount(index, e.target.value)}
              className="border border-blue-300"
            />
          ))}
        </div>
      </Card>
    </div>
  );
}
